header {
    width: 100vw;
    height: 15vh;
    background-color: skyblue;
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    padding: 10px 15px;
    border-radius: 30px;
    border: none;
    outline: 0;
    width: 40%;
    font-size: 22px;
    margin: 0 20px;
}

button {
    padding: 10px 15px;
    border-radius: 20px;
    outline: 0;
    border: none;
    font-size: 16px;
    text-transform: uppercase;
    cursor: pointer;
}

button:hover {
    background-color: blanchedalmond;
}

input:focus {
    border: 1px solid black;
}

input:focus::placeholder {
    color: transparent;
}
